	background: teal;
}

.LoginBlock {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 70px;
	height: 70px;
	cursor: pointer;
}

.LoginBlock a {
	color: white;
	font-weight: 600;
	font-size: 15px;
	text-decoration: none;
}